.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 15px;
    position: sticky;
    z-index: 10;
    background-color: #fff;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
}
.header__left> img {
    height: 40px;
}
.header__left {
    display: flex;
    align-items: center;
}
.header__input {
    display: flex;
    align-items: center; 
    background-color: #eff2f5;   
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}
.header__input> input {
    border: none;
    outline: 0;
    background-color: transparent;
}
.header__center {
    display: flex;
    flex: 1;
    justify-content: center;
}
.header__option .MuiSvgIcon-root {
    color: gray;
}
.header__option:hover .MuiSvgIcon-root {
    color: #2e81f4;
}
.header__option {
    display: flex;
    align-items: center;
    padding: 10px 35px;
    cursor: pointer;
}
.header__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
}
.header__option.active .MuiSvgIcon-root {
    color: #2e81f4;
}
.header__option.active {
    border-bottom: 4px solid #2e81f4;
}
.header__option.active:hover {
    border-radius: 0px;
    background-color: transparent;
}
.header__avatar_info {
    display: flex;
    align-items: center;
}
.header__avatar_info h4 {
    margin-left: 10px;
}
.header__right {
    display: flex;
}
.header__right_last {
    display: flex;
}